
// select LANGUAGE
$('.select__language-top').on('click', function(event) {
  event.preventDefault();
  $('.header__select-language').toggleClass('is-active');
});


$('.select__item-language').on('click', function(event) {
  event.preventDefault();
  var html = $(this).html();
  $('.select__language-top').html(html);
  $('.header__select-language').removeClass('is-active');
  $('.select__item-language').removeClass('remove');
  $(this).addClass('remove');
});





